import React from 'react';
import styles from './Navbar.module.scss';
class Navbar extends React.Component {
    state = {
        nav : false,
    }
    componentDidMount = () => {
        setInterval(this.CheckNav,250);
    }
    CheckNav = () => {
        if(window.pageYOffset > 3){
            this.setState({nav : true});
        } else {
            this.setState({nav : false});
        }
    }
    render() {
        return (
            <div className={this.state.nav ? styles.nav2 : styles.nav}>
                <h2>Scenic</h2>
                <div>
                    <h3>HOME</h3>
                    <h3>STUDIO</h3>
                    <h3>OUR PEOPLE</h3>
                    <h3>LET'S TALK</h3>
                </div>
            </div>
    )
    }
}
    

export default Navbar;